body {
  font-family: "Italianno", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.1rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

thead th, tfoot th {
  font-family: "Rock Salt", cursive;
}

th {
  letter-spacing: 2px;
}

td {
  letter-spacing: 1px;
}

tbody td {
  text-align: center;
}

tfoot th {
  text-align: right;
}

.header {
  background-size: cover;
  background-position: 50%;
  padding: 2rem 0;
  position: relative;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.header__guitarra {
  display: none;
}

@media (min-width: 992px) {
  .header__guitarra {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    /*animation-name: mostrarImagen;
    animation-duration: 1s;
    animation-delay: 0s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1; */
    animation: 1s ease-in-out 0s 1 mostrarImagen;
  }
}
@keyframes mostrarImagen {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
  }
}
.navegacion__link {
  font-size: 2.4rem;
  margin-bottom: 2rem;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}
.navegacion__link:hover {
  color: #FF2E63;
}

@media (min-width: 768px) {
  .bloques__grid {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: dense;
  }
}

.bloque {
  height: 40rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.bloque:nth-child(1) {
  background-color: #212121;
}
.bloque:nth-child(2) {
  background-image: url("../img/img30.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.bloque:nth-child(3) {
  background-color: #0D7377;
}
@media (min-width: 768px) {
  .bloque:nth-child(3) {
    grid-column: 2/3;
  }
}
.bloque:nth-child(4) {
  background-image: url("../img/img37.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.bloque__contenido {
  max-width: 55rem;
  border-block: 1px solid #F0A04B;
  color: #FFFFFF;
  padding: 1rem 0;
}
.bloque__heading {
  text-transform: uppercase;
}
.bloque__texto {
  font-size: 2rem;
}

.nosotros {
  padding: 5rem 0;
}
.nosotros__heading {
  margin-bottom: 5rem;
  text-align: center;
}
.nosotros__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .nosotros__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.nosotros__bloque {
  overflow: hidden;
  padding: 2rem;
  text-align: center;
}
.nosotros__imagen {
  width: 80%;
  transition-property: transform;
  transition-duration: 300ms;
  padding: 7px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.nosotros__imagen:hover {
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .nosotros__imagen {
    padding: 10px;
  }
}
.nosotros__bloque-heading {
  font-size: 2.4rem;
  color: #323232;
  text-align: center;
}
.nosotros__bloque-texto {
  font-size: 1.6rem;
  text-transform: uppercase;
  line-height: 1.8;
}

.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #323232;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 4rem;
}
.bienvenida__heading span {
  color: #000;
  display: block;
  font-family: "Roboto", sans-serif;
}
.bienvenida__texto {
  font-size: 2.6rem;
