  line-height: 1.8;
}

.comentario {
  background-image: linear-gradient(to right, rgba(15, 11, 7, 0.986), rgba(5, 18, 85, 0.753)), url(../img/img33.jpg);
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  padding: 5rem 0;
  text-align: center;
}
.comentario__heading {
  color: #FFFFFF;
  font-size: 6rem;
}
.comentario__texto {
  max-width: 60rem;
  margin: 0 auto;
  color: #FFFFFF;
  line-height: 2;
}
.comentario__enlace {
  background-color: #08D9D6;
  padding: 1.5rem 4rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  padding: 2rem 10rem;
}
@media (min-width: 768px) {
  .comentario__enlace {
    width: auto;
  }
}
.comentario__enlace:hover {
  background-color: #06a8a5;
}
.comentario__hbtn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  overflow: hidden;
  padding: 8px 20px;
  margin: 0px 3px 6px;
  text-align: center;
  border: 2px solid white;
  text-decoration: none;
  color: white;
  white-space: nowrap;
  z-index: 0;
}
.comentario .hbtn i {
  padding-right: 8px;
}
.comentario .hb-border-top-br4 {
  position: relative;
  transition-duration: 0.3s;
  overflow: visible;
  box-sizing: border-box;
  border: none;
  padding: 10px 22px;
}
.comentario .hb-border-top-br4::after {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  border: 2px solid white;
  z-index: 2;
  margin: 0px;
  top: 0px;
  right: 0px;
  border-bottom-width: 2px;
  transition: border-bottom-width 0.1s ease 0.2s, height 0.2s ease 0.1s, width 0.2s ease 0s, margin 0.2s ease 0s, border-top-width 0.2s ease 0s;
}
.comentario .hb-border-top-br4:hover::after {
  width: 60%;
  height: 0px;
  border-width: 4px 2px 0px;
  margin: 0px 20%;
  transition: border-bottom-width 0.1s ease 0s, height 0.2s ease 0.1s, width 0.2s ease 0.2s, margin 0.2s ease 0.2s, border-top-width 0.2s ease 0.2s;
}

